import axios from 'axios'
import { Message } from 'element-ui'
import { mdApiBase } from './config.service'
import localStorageService from './localStorage.service.js'
/** 
 * 超时
 */
axios.defaults.timeout = 1000000
axios.defaults.baseURL = `${mdApiBase}`
axios.defaults.retry = 10
axios.defaults.retryDelay = 1000
/** 
 * 请求拦截
 */
axios.interceptors.request.use(config => {
  if (localStorageService.getItem('Authorization')) {
    config.headers.authorization = localStorageService.getItem('Authorization')
  }
  return config
}, error => {
  // console.log(error);
  return Promise.reject(error)
})
/** 
 * 响应拦截
 */
axios.interceptors.response.use(response => {
  const { data } = response;
  if (response.status === 200) {
    if (data.code === 200 || data.code === 40003 || response.config.responseType === 'blob') {
      return data;
    } else if (data.code === 401) {
      localStorage.clear()
      Message.error('登录超时')
      setTimeout(() => {
        window.location.href = '/login'
      }, 1000);
      return;
    } else {
      Message.error(data.msg || '服务器异常')
      return Promise.reject(response);
    }
  } else {
    Message.error('服务器异常')
    return Promise.reject(response)
  }
}, error => {
  if (error.response) {
    let {
      data,
      status,
    } = error.response
    ResponseStatus(status, data.msg)
  }
  return Promise.reject(error)
})
/** 
 * 响应状态拦截
 */
let ResponseStatus = (status, message) => {
  if (status === 401) {
    localStorage.clear()
    Message.error('登陆超时,即将跳转登录页面...')
  } else {
    Message.error(message)
  }
}
let HttpService = axios
export default HttpService
