import Vue from 'vue'
import VueRouter from 'vue-router'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css' // progress bar style
// import store from '../store'

Vue.use(VueRouter);

const list = [
  // 官网
  {
    path: '/',
    name: 'shared',
    component: () => import('../shared/index.vue'),
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: '首页',
        meta: {
          title: '首页',
          keepalive: false
        },
        component: () => import('../pages/home.vue')
      },
      {
        path: 'kechengjieshao',
        name: '课程介绍',
        meta: {
          title: '课程介绍',
          keepalive: false
        },
        component: () => import('../pages/kechengjieshao.vue')
      },
      {
        path: 'xunmengjiaoyu',
        name: '寻梦教育',
        meta: {
          title: '寻梦教育',
          keepalive: false
        },
        component: () => import('../pages/xunmengjiaoyu.vue')
      },
      {
        path: 'waijiaoshizi',
        name: '外教师资',
        meta: {
          title: '外教师资',
          keepalive: false
        },
        component: () => import('../pages/waijiaoshizi.vue')
      },
      {
        path: 'shenqingshiting',
        name: '申请试听',
        meta: {
          title: '申请试听',
          keepalive: false
        },
        component: () => import('../pages/shenqingshiting.vue')
      }
    ]
  }
]

const routes = [...list];

const router = new VueRouter({
  mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: routes
})

// 防止重复点击路由
const VueRouterPush = VueRouter.prototype.push

VueRouter.prototype.push = function push (to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

/**
 * 
 * @param to
 * @param from
 * @param next()
 */
router.beforeEach((to, from, next) => {
  NProgress.start()
  // const manageList = list.find(e => e.name === 'manage')
  // store.state.manageMenu = manageList.children.filter(e => e.meta.isMenu)

  // const manangeActivityList = manageList.children.find(e => e.path === 'chuangjianhuodong')
  // store.state.manangeActivityMenu = manangeActivityList.children.filter(e => e.meta.isMenu)

  next()
})

/**
 * 
 * @param to
 * @param from
 */
router.afterEach(() => {
  NProgress.done();
})

export default router
