import MdModalToolComponent from "./index.vue";
/** 
 * 模态窗
 */
export const mdModalToolInstall = (Vue) => {
    Object.defineProperty(Vue.prototype, '$mdModal', {
        get() {
            let div = document.createElement('div');
            document.body.appendChild(div);
            return (options) => {
                const Constructor = Vue.extend(MdModalToolComponent);

                new Constructor({
                    data() {
                        return {
                            modalOption: options
                        }
                    },
                }).$mount(div);
            }
        }
    });

    Vue.adModal = Vue.prototype.adModal$;
};